<template>
  <div id="realTimeMonitoring">
    <div class="bak" v-if="toshow" @click="showMenu"></div>
    <div class="menuShow" :class="{rot:toshow}" v-if="!mediaNone" @click="showMenu">
      <span v-if="!toshow">Open</span>
      <span v-if="toshow">Close</span>
    </div>
    <span class="shrink el-icon-d-arrow-left" :class="{menuHide:!$parent.menuPop}" v-show="!sidePop" @click="sidePop=true"></span>
    <!--  -->
    <span class="shrink pop el-icon-d-arrow-right" :class="{menuHide:!$parent.menuPop}" v-show="sidePop" @click="sidePop=false"></span>
    <!--  -->
    <div class="left" :class="{toshow:toshow,sOrh:sidePop}">
      <div class="selectionMode" v-if="mediaNone">
        <div @click="showVideo" :class="[isVideo ? 'isSelectClass' : 'unSelectClass']">
          <div>
            <img src="../../../static/images/videoN.png" alt v-if="isVideo ==false" />
            <img src="../../../static/images/videoY.png" alt v-else />
          </div>
          <span></span>
        </div>
        <div @click="showMap" :class="[!isVideo ? 'isSelectClass' : 'unSelectClass']">
          <div>
            <img src="../../../static/images/mapN.png" alt v-if="isVideo ==true" />
            <img src="../../../static/images/mapY.png" alt v-else />
          </div>
          <span></span>
        </div>
      </div>
      <div class="showBox">
        <!-- <el-radio v-model="selectedCarOrGun" :label="2"></el-radio>
        <el-radio v-model="selectedCarOrGun" :label="1"></el-radio> -->
        <div @click="selectedCarOrGun=2" :class="{isSelcet:selectedCarOrGun==2}"></div>
        <div @click="selectedCarOrGun=1" :class="{isSelcet:selectedCarOrGun==1}"></div>
        <!-- <div @click="selectedCarOrGun='3'" :class="{isSelcet:selectedCarOrGun=='3'}"></div> -->
      </div>

      <div class="selectBox">
        <el-input placeholder="()" v-model="filterCar" :clearable="true" v-show="selectedCarOrGun==2"></el-input>
        <el-input placeholder="()" v-model="filterGun" :clearable="true" v-show="selectedCarOrGun==1"></el-input>
        <el-input placeholder="()" v-model="filterTerminal" :clearable="true" v-show="selectedCarOrGun==3"></el-input>
      </div>
      <div class="trailBox">
        <div></div>

        <el-switch v-model="realTimeTrailValue" class="realTimeTrail"></el-switch>
      </div>
      <div class="treeBox">
        <my-tree :data="data" @node-contextmenu="rightClick" show-checkbox ref="tree" node-key="id" :rend1er-after-expand="false" :default-expand-all="false" :props="defaultProps" @check="getCheckedNodes" @node-click="selectVideoCar" :default-expanded-keys="defaultSelect" @doubleClick="doubleClickTreeNode" :render-content="renderContent" v-show="selectedCarOrGun==2" :expand-on-click-node="false" :filter-node-method="filterNode"></my-tree>

        <div id="handelmenu" v-show="menuVisible" @mouseleave="menuVisible=!menuVisible">

          <el-card class="box-card">
            <div class="text item">
              <el-link :underline="false" @click="rightClickEvent(3)"></el-link>
            </div>
            <div class="text item">
              <el-link :underline="false" @click="rightClickEvent(4)"></el-link>
            </div>
            <!-- <div class="text item">
              <el-link :underline="false" @click="rightClickEvent(3)"></el-link>
            </div>
            <div class="text item">
              <el-link :underline="false" @click="rightClickEvent(4)"></el-link>
            </div> -->
            <div class="text item">
              <el-link :underline="false" @click="rightClickEvent(5)"></el-link>
            </div>
          </el-card>

        </div>
        <my-tree :data="gunData" show-checkbox ref="treeGun" node-key="id" :render-after-expand="false" :default-expand-all="false" :props="defaultPropsGun" @check="getCheckedNodesGun" :render-content="renderContent" :default-expanded-keys="defaultSelectGun" v-show="selectedCarOrGun==1" :expand-on-click-node="false" :filter-node-method="filterNode"></my-tree>
        <my-tree :data="beidouTerminalData" show-checkbox ref="treeBeidouTerminal" node-key="id" :render-after-expand="false" :default-expand-all="false" :props="defaultPropsTerminal" @doubleClick="doubleClickTerminalTreeNode" @check="getCheckedNodesTerminal" :default-expanded-keys="defaultSelectTerminal" v-show="selectedCarOrGun==3" :expand-on-click-node="false" :filter-node-method="filterNode"></my-tree>
      </div>
      <div class="consistency" v-if="mediaNone">
        <el-collapse v-model="activeNames">
          <el-collapse-item title="" name="1">
            <div class="content">
              <div class="selectionMode selectionMode1" v-if="mediaNone">
