                  <div>
                    <img src="../../../static/images/talkN.png" alt v-if="!(radio==1)" />
                    <img src="../../../static/images/talkY.png" alt v-else />
                  </div>
                  <span></span>
                </div>
                <div @click="changeRadio('2')" :class="[!(radio=='1') ? 'isSelectClass' : 'unSelectClass']">
                  <div>
                    <img src="../../../static/images/listenN.png" alt v-if="radio==1" />
