                <template slot-scope="scope">
                  <span>{{formatCarOrGunType(scope.row)}}</span>
                </template>
              </el-table-column>
              <el-table-column label="" width="200px" align="center">
                <template slot-scope="scope">
                  <span>{{formatDate(parseInt(scope.row.info.time) ) }}</span>
                </template>
              </el-table-column>
              <el-table-column label="" min-width="200px" align="center">
                <template slot-scope="scope">
                  <span>{{getWarningType(scope.row,scope.row.type)}}</span>
                </template>
              </el-table-column>
              <!-- <el-table-column label="" min-width="200px" align="center">
                <template slot-scope="scope">
                  <span
                    v-if="scope.row.isCancled&&(scope.row.status!==10007||(scope.row.status!==10007&&scope.row.type==1))"
                  ></span>
                  <el-button
                    v-if="scope.row.status==10011&&scope.row.isCancled==false"
                    type="warning"
                    size="mini"
                    @click="cancle(scope.row.imei)"
                  ></el-button>
                </template>
              </el-table-column>-->
              <el-table-column label="" min-width="200px" align="center" fixed="right">
                <template slot-scope="scope">
                  <el-button v-if="scope.row.status==10011&&scope.row.isCancled==false" type="primary" size="mini" class="bottomUnActiveBtn" @click="cancle(scope.row.imei)"></el-button>
                  <el-button type="primary" @click="handleAlarm1(scope.row)" v-show="scope.row.info.uuid" size="mini" class="bottomUnActiveBtn"></el-button>
                  <el-button type="primary" size="mini" class="bottomUnActiveBtn" @click="delayAlarm1(scope.row)" v-show="scope.row.info.uuid"></el-button>
                </template>
              </el-table-column>
              <el-table-column label="" min-width="180px" align="center">
