                  <el-button type="warning" size="mini" @click="deleteNetwork(row)" v-if="row.check==0" class="bottomActiveBtn"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="" name="three">
            <el-table v-loading="listLoading" :data="list_route" fit highlight-current-row style="width: 99%;" :row-class-name="tableRowClassName">
              <el-table-column label="" prop="id" align="center" width="60" type="index" :index="indexMethod_route"></el-table-column>
              <el-table-column label="" min-width="150px" align="center" prop="name"></el-table-column>
              <el-table-column label="" width="200px" align="center" prop="createTime"></el-table-column>

              <el-table-column label="" align="center" min-width="230" class-name="small-padding fixed-width">
                <template slot-scope="{row}">
                  <el-button type="primary" size="mini" @click="checkRoute(row)" :disabled="!row.check" class="bottomUnActiveBtn" v-if="row.check"></el-button>
                  <el-button type="warning" size="mini" @click="deleteSingleRoute(row)" v-if="!row.check" class="bottomActiveBtn"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="" name="four" v-if="power==1">
            <el-table :data="offlineTimeoutArr.slice((currpage1 - 1) * pagesize1, currpage1 * pagesize1)" @selection-change="handleSelectionChange" :key="tableKey" v-loading="listLoading" fit highlight-current-row style="width: 99%;" :row-class-name="tableRowClassName">
              <el-table-column label="" prop="id" sortable="custom" align="center" width="80" type="index"></el-table-column>
              <el-table-column label="" width="200px" align="center">
                <template slot-scope="{row}">
                  <span>{{ $store.state.realTimeMonitor.carAndGunObj[row.imei]?$store.state.realTimeMonitor.carAndGunObj[row.imei].code:''}}</span>
                </template>
              </el-table-column>
              <el-table-column label="" width="200px" align="center">
                <template slot-scope="{row}">
                  <span>{{ row.imei}}</span>
                </template>
              </el-table-column>
              <el-table-column label="" width="200px" align="center">
                <template>
                  <span>{{offlineType==2?'':''}}</span>
                </template>
              </el-table-column>
              <el-table-column label="" width="200px" align="center">
                <template slot-scope="scope">
                  <span>{{formatDate(parseInt(scope.row.time) ) }}</span>
                </template>
              </el-table-column>
              <el-table-column label="" min-width="200px" align="center">
                <template slot-scope="scope">
                  <span>{{scope.row.offlineTime}}</span>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <!-- <el-tab-pane label="" name="five">
            <el-table :data="beidouTerminalMes.slice((currpage2 - 1) * pagesize2, currpage2 * pagesize2)" @selection-change="handleSelectionChange" :key="tableKey1" v-loading="listLoading" fit highlight-current-row style="width: 99%;" :row-class-name="tableRowClassName">
              <el-table-column label="" prop="id" sortable="custom" align="center" width="80" type="index"></el-table-column>

              <el-table-column label="id" width="200px" align="center">
                <template slot-scope="{row}">
                  <span>{{ row.id}}</span>
                </template>
              </el-table-column>
              <el-table-column label="" width="200px" align="center">
                <template slot-scope="scope">
                  <span>{{formatDate(parseInt(scope.row.time) ) }}</span>
                </template>
              </el-table-column>

              <el-table-column label="" min-width="200px" align="center">
                <template slot-scope="scope">
                  <span v-if="scope.row.lng&&scope.row.lat">{{':'+scope.row.lng+' :'+scope.row.lat}}</span>
                </template>
              </el-table-column>
              <el-table-column label="" min-width="200px" align="center">
                <template slot-scope="scope">
                  <span>{{scope.row.content}}</span>
                </template>
              </el-table-column>
              <el-table-column label="" align="center" min-width="230" class-name="small-padding fixed-width">
                <template slot-scope="{row}">
                  <el-button type="primary" size="mini" class="sendBtn" @click="sendMes(row)"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane> -->
        </el-tabs>
      </div>
    </div>
    <el-dialog title="" class="realTimeDialog" :visible.sync="dialogSendVisible" :close-on-click-modal="false" center>
      <el-form :model="sendForm" status-icon :rules="sendRules" ref="sendForm">
        <el-form-item label="ID" prop="id">
          <el-input type="text" placeholder="" v-model="sendForm.id" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="formatContent">
          <el-input type="textarea" v-model="sendForm.formatContent"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitSendMes()"></el-button>
      </div>
    </el-dialog>
    <el-dialog :title="dealOrDelayTitle" :visible.sync="dealVisible" append-to-body :close-on-click-modal="true" class="alarmDealBox realTimeDialog" center>
      <el-form ref="dataForm" :model="dealTemp" label-position="left" :rules="rules" center>
        <el-form-item label="" prop="info">
          <el-input type="textarea" v-model="dealTemp.info" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="handleAlarmSubmit()"></el-button>
      </div>
    </el-dialog>
    <el-dialog :title="dealOrDelayTitle" :visible.sync="innerDelayVisible" append-to-body :close-on-click-modal="true" class="alarmDelayBox realTimeDialog" center>
      <el-form ref="delayForm" :rules="rules" :model="delayTemp" label-position="left">
        <el-form-item label="()" prop="time">
          <el-input v-model.number="delayTemp.time" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="delayOneAlarmSubmit()"></el-button>
      </div>
    </el-dialog>
    <!-- <el-dialog title="" :visible.sync="pasVisible" append-to-body :close-on-click-modal="true" center>
      <el-form ref="pasForm" :rules="pasRules" :model="pasTemp" label-position="left">
        <el-form-item label="" prop="pas">
          <el-input v-model="pasTemp.pas" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="rightSubmit()"></el-button>
      </div>
    </el-dialog> -->
    <el-dialog title="" :visible.sync="addNetworkVisible" append-to-body :close-on-click-modal="true" center>
      <el-form ref="addNetworkTemp" :rules="addNetworkrRules" :model="addNetworkTemp" label-position="left" class="formStyle">
        <el-form-item prop="networkVal" label="">
          <el-select v-model="addNetworkTemp.networkVal" class="filter-item" placeholder="">
            <el-option v-for="item in networkTypeOptions" :key="item.type" :label="item.option" :value="item.type" />
          </el-select>
        </el-form-item>
        <el-form-item prop="bank" label="">
          <el-select filterable v-model="addNetworkTemp.bank" class="filter-item" placeholder="">
            <el-option v-for="item in bankOptions" :key="item.id" :label="item.name" :value="item.id" :disabled="item.status==2" />
          </el-select>
        </el-form-item>
        <el-form-item prop="roadName" label="">
          <el-input placeholder="" v-model="addNetworkTemp.roadName" />
        </el-form-item>
        <el-form-item prop="needToReport" label="">
          <el-select v-model="addNetworkTemp.needToReport" class="filter-item" placeholder="">
            <el-option v-for="item in reportOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item prop="sendTime" label="">
          <el-time-picker v-model="addNetworkTemp.sendTime" value-format="HH:mm:ss" :picker-options="{selectableRange: '00:00:00 - 23:59:59'}" placeholder=""></el-time-picker>
        </el-form-item>
        <el-form-item prop="receiveTime" label="">
          <el-time-picker v-model="addNetworkTemp.receiveTime" value-format="HH:mm:ss" :picker-options="{selectableRange: '00:00:00 - 23:59:59'}" placeholder=""></el-time-picker>

        </el-form-item>
