  methods: {
    handleClickP(){

    },
    addNetworkSubmit() {
      this.$refs['addNetworkTemp'].validate((valid, obj) => {
        if (valid) {
          let _t = this;
          let myData = {
            name: _t.addNetworkTemp.roadName,
            address: _t.addNetworkTemp.networkSearchVal,
            type: _t.addNetworkTemp.networkVal,
            lng: _t.addNetworkTemp.lng,
            lat: _t.addNetworkTemp.lat,
            radius: _t.addNetworkTemp.radius,
            needToReport: _t.addNetworkTemp.needToReport
          };
          if (_t.addNetworkTemp.sendTime) {
            myData.sendTime = _t.addNetworkTemp.sendTime;
          }
          if (_t.addNetworkTemp.receiveTime) {
            myData.receiveTime = _t.addNetworkTemp.receiveTime;
          }
          let obj = {};
          obj = this.bankOptions.find((item) => {
            return item.id === _t.addNetworkTemp.bank;
          });
          let bankName = ''
          bankName = obj.name;
          myData.parentBankId = _t.addNetworkTemp.bank;
          myData.parentBankName = bankName

          this.$refs['addNetworkTemp'].resetFields();
          addlstPt(myData)
            .then(res => {
              if (res.success) {
                _t.addNetworkVisible = false;
                _t.$message.success("");
                _t.addNetworkTemp = {
                  roadName: "", // 
                  networkSearchVal: "", // 
                  networkVal: "", // 
                  radius: 50, // 
                  bank: "",
                  sendTime: "",
                  receiveTime: "",
                  needToReport: 2,
                }
              } else {
                _t.$message.error(res.msg);
              }
            })
            .finally(() => {
              _t.myLoading = false;
            });
        }
      })
    },
    //
    getSelect() {
      let that = this;
      selectBank().then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          that.bankOptions = res.data;
        } else {
          if (res.data == null) {
            this.bankOptions = [];
          }
          this.$message.error(res.msg);
        }
      });
    },
    talkReq() {
      if (this.openState) {
        this.$message.warning('');
        return;
      }
      this.activeNames = ["1"]//
      this.changeRadio('1');
      this.talkAndMonitor.imei = this.selectTalkData.deviceNo;
      this.talkAndMonitor.licenseNo = this.selectTalkData.licenseNo;
      this.$parent.deleteRow();
      // if (this.isInitFinished == false) {
      if (!this.isVideo) {
        this.showVideo(); //
      }

      let times = 0;

      this.talkInterval = setInterval(() => {
        times++;
        if (this.isInitFinished == true && this.selectTalkData.hasOwnProperty('deviceNo')) {

          this.open();
          window.clearInterval(this.talkInterval);
          this.talkInterval = null;


        } else if (this.isInitFinished == false && this.selectTalkData.hasOwnProperty('deviceNo')) {
          if (times >= 60) {
            window.clearInterval(this.talkInterval);
            this.talkInterval = null;
            this.$message.warning('flash')

          }
        }

      }, 500)

      // } else {

      //   this.open();
      // }


      // setTimeout(() => {
      //     this.activeNames = ["1"]//

      // this.talkAndMonitor.imei = this.selectTalkData.deviceNo;
      // this.talkAndMonitor.licenseNo = this.selectTalkData.licenseNo
      //   this.open();


      // }, 2000)
    },
    rightClickEvent(CtrlType) {
      // this.pasTemp.CtrlType = CtrlType;
      let Usr = 'beidou';
      let md5 = 'ad94d37d7cb6b66d7e299af04633dc0b';
      if (location.hostname = 'www.beidoulab.work') {
        Usr = 'admin'
        md5 = 'e9ebe76d573963b1220c01de7d0ae650';
      }

      vehicleControlOthers({ DevIDNO: this.nodeData.devno, CtrlType: CtrlType, Usr: Usr, Pwd: md5 }).then(res => {
        if (res.result == 0) {
          this.$message.success('');

        }

      }).catch((err) => {
        this.$message.error(err);

      })

    },
    rightClick(MouseEvent, object, Node, element) {
      if (object.dType == 2) {
        this.nodeData = object;
        this.menuVisible = true;
        let menu = document.querySelector("#handelmenu");
        menu.style.cssText = "position: fixed; left: " + (MouseEvent.clientX + 25) + 'px' + "; top: " + (MouseEvent.clientY - 10) + 'px; z-index: 999; cursor:pointer;';
      }
    },

    //type 0: 1
    changeChnColor(cameraLossing, videoLossing, imei) {
      for (let i = 0; i < 8; i++) {
        let statusCameraLossing = cameraLossing >>> (8 - i - 1);
        let statusVideoLossing = videoLossing >>> (8 - i - 1);
        let str1 = '.iconshishishipin.c' + i + 's' + imei
        let str2 = '.iconluxiang.c' + i + 's' + imei
        statusCameraLossing ? $(str1).css('color', '#3C7EFE') : $(str1).css('color', '#ccc')
        statusVideoLossing ? $(str2).css('color', '#3C7EFE') : $(str2).css('color', '#ccc')
      }
    },
    changeFullScreen() {
      if (
        window.screenLeft == 0 &&
        window.document.body.clientWidth == window.screen.width
      ) {
        $("#videoBox").width("933px");
        $("#video").width("933px");
        this.winResize = 'winS-0'

      } else {
        $("#videoBox").width("1212px");
        $("#video").width("1212px");
        $("#video").height("100%");
        this.winResize = 'winS-1'

      }
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    //
    sendMes(row) {
      this.dialogSendVisible = true;
      if (row) {
        this.sendForm.id = row.id;
      }
    },
    //
    submitSendMes() {
      this.$refs["sendForm"].validate(valid => {
        if (valid) {
          sendTerminal(this.sendForm).then(res => {
            if (res.status == 200) {
              //
              if (this.getCountDown(res.data) == 0) {
                this.$message.success("");
                //
              } else {
                this.$message.success(this.getCountDown(res.data));
              }
            }
            this.sendForm = {};
            this.dialogSendVisible = false;
          });
        } else {
          return false;
        }
      });
    },
    //
    initMap() {
      let that = this;
      this.map = new BMap.Map("map", { enableMapClick: false });
      this.map.centerAndZoom("", 13); //
      //
      this.map.addControl(
        new BMap.MapTypeControl({
          mapTypes: [BMAP_NORMAL_MAP, BMAP_HYBRID_MAP]
