          this.selectAllOrPart = "all";
          this.selectImei = null;
        }
      }
    },

    //
    getCheckedNodesGun(data, node) {
      // 
      let array = this.$refs.treeGun.getCheckedNodes(false, false);
      //  item.type=="device",
      let filterArray = array.filter(item => {
