    //
    open() {
      if (this.talkInterval) {
        window.clearInterval(this.talkInterval);
        this.talkInterval = null;
      }
      if (this.isVideo == false) {
        this.$message.warning("");
        return;
      }
      if (this.talkAndMonitor.imei == null && this.radio == "1") {
        this.$message.error("");
        return;
      } else if (
        (this.talkAndMonitor.imei == null || this.talkAndMonitor.chn == null) &&
        this.radio == "2"
      ) {
        this.$message.error("");
        return;
      }
      this.openState = true;
      // 
      if (this.radio == "1") {
        this.startTalkback(this.talkAndMonitor.imei);
      } else if (this.radio == "2") {
        this.startMonitor(this.talkAndMonitor.imei);
      }
    },
    //
    stop() {
      if (this.isVideo == false) {
        this.$message.warning("");
        return;
      }
      // 
      // 
      if (this.radio == "1") {
        swfobject.getObjectById(this.divId).stopTalkback();
        this.talkhintText = '';
      } else if (this.radio == "2") {
        // 
        swfobject.getObjectById(this.divId).stopListen(); // 
      }

      // 
      this.openState = false;
    },
    // :2020-03-11 16:23:52
    formatDate(timestamp) {
      if (isNaN(timestamp)) {
        return '';
      } else {
        let date = new Date(timestamp);
        let year = date.getFullYear();
        let month = this.addZero(date.getMonth() + 1);
        let day = this.addZero(date.getDate());
        let hours = this.addZero(date.getHours());
        let minutes = this.addZero(date.getMinutes());
        let seconds = this.addZero(date.getSeconds());

        return (
          year +
          "-" +
          month +
          "-" +
          day +
          " " +
          hours +
          ":" +
          minutes +
          ":" +
          seconds
        );
      }
    },
    formatDate1(timestamp) {
      if (isNaN(timestamp)) {
        return null;
      } else {
        let date = new Date(timestamp);
        let year = date.getFullYear();
        let month = this.addZero(date.getMonth() + 1);
        let day = this.addZero(date.getDate());

        return year + "_" + month + "_" + day;
      }
    },
    //0
    addZero(num) {
      return num < 10 ? "0" + num : num;
    },

    addCarToMap(message, type, alarm) {
      //type: 012gps3 40 5fake 6:
      //1 023type
      // 40sessionStorage.aloneObj0 5fake
      //sessionStorage.aloneObjtype

      // type01marker
      if (!message.lng && !message.lat && type == 0) {
        return;
      }
      let that = this;
      let repeatIndex = null; //imei
      let img; //
      let pointArr = []; //pointArr[0]:
      let ggPoint = {}; //
      let convertorData = []; //
      let Blng = null; //
      let Blat = null;
      let Icon = null; //marker
      let point = {}; //
      let aloneObj1 = JSON.parse(sessionStorage.aloneObj); //
      for (let index = 0; index < that.makerIconArr.length; index++) {
        if (that.makerIconArr[index].imei == message.imei) {
          repeatIndex = index;
          break;
        }
      }
      //
      if (repeatIndex == null && (!message.lng || !message.lat)) {
        return;
      }
      //,type
      if (aloneObj1.hasOwnProperty(message.imei)) {
        if (aloneObj1[message.imei] !== 10007) {
          if (message.type == 2) {
            img = aloneCar;
          } else if (message.type == 1) {
            img = aloneGun;
          }
        } else {
          if (message.type == 2) {
            img = gpsCar; //0
          } else if (message.type == 1) {
            img = gpsGun;
          }
        }
      } else {
        if (type == 0 || type == 2) {
          if (message.type == 2) {
            img = offlineCar;
          } else {
            img = offlineGun;
          }
        } else if (type == 3) {
          if (message.type == 2) {
            img = gpsCar2;
          } else {
            img = gpsGun2;
          }
        } else if (type == 1) {
          if (message.type == 2) {
            img = onlineCar;
          } else {
            img = onlineGun;
          }
        } else if (type == 4) {
          if (message.type == 2) {
            img = gpsCar;
          } else {
            img = gpsGun;
          }
        } else if (type == 5) {
          if (message.type == 2) {
            img = fakecar;
          } else {
            img = fakegun;
          }
        } else if (type == 6) {
          if (message.type == 2) {
            img = noPositionCar;
          } else {
            img = noPositionGun;
          }
        }
      }
      // console.warn(message);
      //   console.warn('status',type);
      // console.warn('speed',message.speed);
      // console.warn('lng',message.lng);
      //  console.warn('img1',img);
      if ((img == onlineCar || img == fakecar) && that.speedObj[message.imei] == 1) {
        img = speedCar;
      }
      if ((img == onlineGun || img == fakegun) && that.speedObj[message.imei] == 1) {
        img = speedGun;
      }

      // console.warn('img2',img);


      if (repeatIndex !== null && alarm == 'alarm') {
        //
        let marker11 = new BMap.Icon(img, this.bmapSize, {
          imageSize: this.bmapSize
        });
        that.makerIconArr[repeatIndex].Icon.setIcon(marker11);

        if (message.type == 2) {
          that.makerIconArr[repeatIndex].Icon.setRotation(that.makerIconArr[repeatIndex].Icon.getRotation());

          // that.makerIconArr[repeatIndex].IconLabel.setRotation(that.makerIconArr[repeatIndex].IconLabel.getRotation());

        }
        return;
      }
      //
      if (!message.lng && !message.lat && repeatIndex !== null) {
        //0repeatIndex
        ggPoint = new BMap.Point(
          that.makerIconArr[repeatIndex].lng,
          that.makerIconArr[repeatIndex].lat
        );
      } else if (message.lng && message.lat) {
        ggPoint = new BMap.Point(message.lng, message.lat);
      }
      //
      pointArr.push(ggPoint);
      convertorData = Gps2Bds(pointArr);
      if (convertorData.length !== 0) {
        Blng = convertorData[0].lng;
        Blat = convertorData[0].lat;

        point = new BMap.Point(Blng, Blat);
        if (!this.rayCasting(Blat, Blng, this.CreateChinaMapLine())) {
          return;
        }
        //    //
        //    let myGeo = new BMap.Geocoder(); // 

        // myGeo.getLocation(point, function(result) {
        //   if(result){
        //     // console.warn(result.addressComponents.province)
        //   }
        // })
        //   //
        let marker = new BMap.Icon(img, this.bmapSize, {
          imageSize: this.bmapSize
        });

        let lng = message.lng;
        let lat = message.lat;
        let direction = 0;
        if (message.direction !== undefined && message.direction !== null) {
          direction = message.direction + 90;
        } else {
          direction = 90;
        }
        //
        if (repeatIndex !== null) {
          //
          if (type == 2 || type == 3 || type == 4||type==6) {
            lng = that.makerIconArr[repeatIndex].lng;
            lat = that.makerIconArr[repeatIndex].lat;
          }
          else {
            lng = message.lng;
            lat = message.lat;
          }
          //
          if (type == 0 || type == 1 || type == 5) {
            that.makerIconArr[repeatIndex].Icon.setPosition(point);
            // that.makerIconArr[repeatIndex].IconLabel.setPosition(point);

          }
          //setIcon
          that.makerIconArr[repeatIndex].Icon.setIcon(marker);

          //
