    //
    getWarningType(row, type) {
      if (row.status == 10001) {
        if (type == 1) {
          return "";
        } else {
          return "";
        }
      } else if (row.status == 10002) {
        return "GPS";
      } else if (row.status == 10003) {
        return "";
      } else if (row.status == 10004) {
        return "";
      } else if (row.status == 10005) {
        return "";
      } else if (row.status == 10006) {
        if (type == 1) {
          return "";
        } else {
          return "";
        }
      } else if (row.status == 10007) {
        if (type == 1) {
          if (row.info.value !== null && row.info.value !== undefined) {
            return " :" + row.info.value + "%";
          } else {
            return "";
          }
        } else {
          if (row.info.value) {
            return " :" + row.info.value + "km/h";
          } else {
            return "";
          }
        }
      } else if (row.status == 10008) {
        if (row.info.lng && row.info.lat) {
          return (
            " " + ":" + row.info.lng + "," + ":" + row.info.lat
          );
        } else {
          return "";
        }
      } else if (row.status == 10009) {
        if (type == 22) {
          return "";
        } else if (type == 300) {
          return "";
        }
      } else if (row.status == 10011) {
        if (type == 22) {
          return "";
        } else if (type == 300) {
          return "";
        }
      } else if (row.status == 10070) {
        return ""
      } else if (row.status == 10080) {
       return row.info.gunCode+'';
      }
    },
    //renderContent
    renderContent(h, { node, data, store }) {
      let that = this;
      // if (data.type !== "device") {
      //   return (
      //     <span>
      //       <i class={data.className}></i>
      //       <span>{node.label}</span>
      //     </span>
      //   );
      // } else 
      if (data.type == "device") {
        return (
          <span>
            <i class={data.className}></i>
            {data.dType == 2 ? (
              <i class={"iconfont  signal iconxinhaoge1 s" + data.id}></i>
            ) : (
              ""
            )}
            {data.dType == 2 ? (
              <i class={"iconfont   iconcloud-disk d" + data.id}></i>
            ) : (
              ""
            )}
            <span
              class={"tree-car p" + data.id}
              on-click={e => {
                if (node.checked == true) {
                  if (e.target.className == "tree-car p" + data.id) {
                    //
                    $("span").removeClass("tree-car-selected");
                    e.target.className =
                      e.target.className + " tree-car-selected";
                    $(".trail").remove();
                    //
                    this.followCarOrGun = data.id;
                    this.richMarkerImeiIndex = data.id;
                    $(e.target).after(
                      `<div class="trail"><img class="change trail ${this.followCarOrGun == null ? "none" : ""
                      }"  title="()" src="${trailImg}" /></div>`
                    );
                    sessionStorage.historyCode = JSON.stringify(data); // 
                    this.setZindex(this.followCarOrGun);
                    let message = null;
                    //
                    //
                    for (let i = 0; i < that.makerIconArr.length; i++) {
                      if (that.makerIconArr[i].imei == data.id) {
                        message = that.makerIconArr[i];
                        that.addressObj.lastTime = null;
                        that.addressObj.address = '';
                        that.openInfo(
                          message,

                          "changeRichMarkerInfo"
                        );
                        let point = new BMap.Point(message.Blng, message.Blat);
                        that.map.panTo(point);
                        that.myRichMarkerDraged = false;
                        that.myRichMarker.show();

                        break;
                      }
                    }
                    //
                    for (let item in this.carMkObj) {
                      if (item == data.id) {
                        message = this.carMkObj[item].message;
                        that.addressObj.lastTime = null;
                        that.addressObj.address = '';
                        that.openInfo(
                          message,

                          "changerRealTrail"
                        );
                        let point = this.carMkObj[item].message.point;
                        that.map.panTo(point);
                        that.myRichMarkerDraged = false;
                        that.myRichMarker.show();
                      }

                    }
                    if (!message) {
                      that.myRichMarkerDraged = false;
                      that.myRichMarker.hide();
                    }
                  } else {
                    //
                    e.target.className = "tree-car p" + data.id;
                    this.followCarOrGun = null;
                    that.myRichMarkerDraged = false;
                    that.myRichMarker.hide();
                    that.richMarkerImeiIndex = -1;
                    $(".trail").remove();
                  }
                }
              }}
            >
              {node.label}
            </span>
          </span>
        );
      } else if (data.type == "camera") {
        return (
          <span>
            <i class={"iconfont   iconshishishipin c" + data.index + 's' + data.parentId}></i>
            <i class={"iconfont    iconluxiang c" + data.index + 's' + data.parentId}></i>
            <span>{node.label}</span>
          </span>
        );
      } else {
        return (
          <span>
            <i class={data.className}></i>
            <span>{node.label}</span>
          </span>
        );
      }
    },

