        }
      }
    },

    //
    //gunOrCarType'car''gun'()
    //isInline 1: 5fake 2gps 3
    //i, j 
    //imei
    changeTreeOnline(gunOrCarType, isInline, imei) {
      let obj = {},
        data = [],
        that = this;
      if (gunOrCarType == "car") {
        obj = this.carIsOnlineArr;
        data = this.data;
      } else if (gunOrCarType == "gun") {
        obj = this.gunIsOnlineArr;
        data = this.gunData;
      }

      //
      if (obj.hasOwnProperty(imei)) {
        if (obj[imei] !== isInline) {
          if (
            (obj[imei] == 1 || obj[imei] == 5||obj[imei] == 6|| obj[imei] == 3) &&
            (isInline == 2  || isInline == 0)
          ) {
            //
            that.getTreeOnlineNum(gunOrCarType, "reduce", imei);
          }
          if (
            (obj[imei] == 2  || obj[imei] == 0) &&
            (isInline == 1 || isInline == 5||isInline == 6|| isInline == 3)
          ) {
            //
            that.getTreeOnlineNum(gunOrCarType, "add", imei);
          }
        }
      } else {
        //
        if (isInline == 1 || isInline == 5|| isInline == 6|| isInline == 3) {
          that.getTreeOnlineNum(gunOrCarType, "add", imei);
        }
      }
    },
    //
    //gunOrCarType car: gun:
    //i,j for
    //addOrReduce:'add' 'reduce':
    getTreeOnlineNum(gunOrCarType, addOrReduce, imei) {
      let data = [];
      if (gunOrCarType == "car") {
        data = this.data;
        name = "";
      } else if (gunOrCarType == "gun") {
        data = this.gunData;
        name = "";
      }
      for (let i = 0; i < data[0].children.length; i++) {
        for (let j = 0; j < data[0].children[i].children.length; j++) {
          if (data[0].children[i].children[j].id == imei) {
            //
            if (addOrReduce == "add") {
              data[0].children[i].isOnlineNum += 1;
              data[0].children[i].name =
                data[0].children[i].teamName1 +
                "(" +
                data[0].children[i].isOnlineNum +
                "/" +
                data[0].children[i].children.length +
                ")";
              data[0].inlineNum += 1;
              data[0].name =
                this.company +
                "(" +
                name +
                data[0].inlineNum +
                "/" +
                data[0].totalNum +
                ")";
            } else if (addOrReduce == "reduce") {
              //
              if (data[0].children[i].isOnlineNum > 0) {
                data[0].children[i].isOnlineNum -= 1;
                data[0].children[i].name =
                  data[0].children[i].teamName1 +
                  "(" +
                  data[0].children[i].isOnlineNum +
                  "/" +
                  data[0].children[i].children.length +
                  ")";
                if (data[0].inlineNum > 0) {
                  data[0].inlineNum -= 1;
                  data[0].name =
                    this.company +
                    "(" +
                    name +
                    data[0].inlineNum +
                    "/" +
                    data[0].totalNum +
                    ")";
                }
              }
            }
            break;
          }
        }
      }
    },

