    //check check = 1
    getNetworkList() {
      // 
      let _t = this;
      _t.listLoading_network = true;
      lstPt({
        type: _t.networkVal,
        page: _t.listQuery_network.page,
        limit: _t.listQuery_network.limit
      })
        .then(res => {
          if (res.status == 0) {
            _t.listLoading_network = false;
            _t.list_network = res.data.records;
            _t.list_network.forEach((item, index) => {
              _t.list_network[index].index = index;
              //check=0,
              //check=1,
              //_t.networkMarkerObj _t.networkPolygonObj
              if (_t.networkMarkerObj.hasOwnProperty(item.id)) {
                _t.list_network[index].check = 0;
              } else {
                _t.list_network[index].check = 1;
              }
              if (_t.networkPolygonObj.hasOwnProperty(item.id)) {
                _t.list_network[index].check = 0;
              } else {
                _t.list_network[index].check = 1;
              }

              _t.list_network[index].createTime = formatDate(
                item.createTime,
                1
              );
              _t.list_network[index].networkType =
                _t.networkList[item.contentType].option;
            });
            _t.total_network = res.data.total;
            _t.indexMethod =
              (_t.listQuery_network.page - 1) * _t.listQuery_network.limit + 1;
          } else {
            _t.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading_network = false;
        });
    },
    // 
    handleFilter(e) {
      this.networkVal = e;
      this.listQuery_network.page = 1;
      this.deleteAllNetwork();

      this.getNetworkList();
    },

    //
    //  

    checkNetwork(row) {
      row.check = 0;
      this.list_network.splice(row.index, 1, row);
      if (row.contentType !== 7) {
        let pointArr = this.getLngAndLat(row.geom);
        this.addNetWork(pointArr[0], pointArr[1], row);
        this.addCircle(pointArr[0], pointArr[1], row);
      } else {
        //
        let geoms = row.geom.slice(9, -2);
        this.drawPolygon(geoms, row);
      }
    },
    //
    deleteNetwork(row) {
      row.check = 1;
      this.list_network.splice(row.index, 1, row);
      this.map.removeOverlay(this.networkMarkerObj[row.id]);
      this.map.removeOverlay(this.networkCircleObj[row.id]);

      delete this.networkMarkerObj[row.id];
      delete this.networkCircleObj[row.id];
      if (row.contentType == 7) {
        this.map.removeOverlay(this.networkPolygonObj[row.id]);
        delete this.networkPolygonObj[row.id];
      }
    },
    //
    deleteSingleRoute(row) {
      row.check = 1;
      this.list_route.splice(row.index, 1, row);
      for (let i = 0; i < this.allRoute.length; i++) {
        if (row.geometryId == this.allRoute[i].geometryId) {
          this.map.removeOverlay(this.allRoute[i].polyline);
          this.map.removeOverlay(this.allRoute[i].marker1);
          this.map.removeOverlay(this.allRoute[i].marker2);
          this.map.removeOverlay(this.allRoute[i].pointCollection);
          this.allRoute.splice(i, 1);
          delete this.networkRouteObj[row.geometryId];
        }
      }
    },
    //
    deleteRoute() {
      for (let i = 0; i < this.list_route.length; i++) {
        this.list_route[i].check = 1;
      }
      let aa = this.list_route[this.list_route.length - 1];
      this.list_route.pop();
      this.list_route.push(aa);
      for (let i = 0; i < this.allRoute.length; i++) {
        this.map.removeOverlay(this.allRoute[i].polyline);
        this.map.removeOverlay(this.allRoute[i].marker1);
        this.map.removeOverlay(this.allRoute[i].marker2);
        this.map.removeOverlay(this.allRoute[i].pointCollection);
      }
      this.allRoute = [];
      this.networkRouteObj = {};
    },
    //  marker
    deleteAllNetwork() {
      if (this.list_network.length == 0) {
        return;
      }
      for (let i in this.networkMarkerObj) {
        this.map.removeOverlay(this.networkMarkerObj[i]);
      }
      for (let i in this.networkCircleObj) {
        this.map.removeOverlay(this.networkCircleObj[i]);
      }
      for (let i in this.networkPolygonObj) {
        this.map.removeOverlay(this.networkPolygonObj[i]);
      }
      for (let i = 0; i < this.list_network.length; i++) {
        this.list_network[i].check = 1;
      }
      let aa = this.list_network[this.list_network.length - 1];
      this.list_network.pop();
      this.list_network.push(aa);
      this.networkMarkerObj = {};
      this.networkCircleObj = {};
      this.networkPolygonObj = {};
    },
