    //
    getLngAndLat(data) {
      let arr = data.split("(")[1];
      arr = arr.split(")")[0];
      arr = arr.split(" ");
      return arr;
    },
    //
    addNetWork(lng, lat, row) {
      let p1 = new BMap.Point(lng, lat);
      let that = this;
      let networkMarker = null;
      networkMarker = new BMap.Marker(p1); // 
      this.networkMarkerObj[row.id] = networkMarker;
      this.map.addOverlay(networkMarker); // 
      let opts = {
        // title: "" // 
        offset: new BMap.Size(-116, 50)
      };
      let html = `<div class="contentBox"><ul class="myNetworkInfo">
            <li><i class="eid"></i></li>
            <li class="${this.power == 1 ? "" : "hide"}">ID<i class="eid">${row.id
        }</i></li>
            <li><i>${row.networkType}</i></li>
             <li><i>${row.name}</i></li>
          </ul></div>`;
      let infoWindow = new BMap.InfoWindow(html, opts); //
      networkMarker.addEventListener("click", function () {
        this.openInfoWindow(infoWindow);
        networkMarker = null;
      });
    },
    //
    addCircle(lng, lat, row) {
      let mPoint = new BMap.Point(lng, lat);
      let networkCircle = new BMap.Circle(mPoint, row.radius, {
        fillColor: "blue",
        strokeWeight: 1,
        fillOpacity: 0.3,
        strokeOpacity: 0.3
      });
      this.networkCircleObj[row.id] = networkCircle;
      this.map.addOverlay(networkCircle);
    },
    // 
    drawPolygon(list, row) {
      let t = this,
        map = t.map,
        paths = list.split(","),
        result = [];
      paths.pop();
      paths.forEach(x => {
        let res = x.split(" ");
        let bmapPoint = new BMap.Point(res[0], res[1]);
        result.push(bmapPoint);
      });
      // 
      let myPolygon = new BMap.Polygon(result, {
        strokeColor: "red", //
        fillColor: "#e4ff02", //
        strokeWeight: 3, //
        strokeOpacity: 0.8, //0 - 1
        fillOpacity: 0.5, //0 - 1
        strokeStyle: "solid" //soliddashed
      });
      myPolygon.setFillColor("orange");
      t.networkPolygonObj[row.id] = myPolygon;

      map.addOverlay(myPolygon);
      let opts = {
        // title: "" // 
      };
      let html = `<div class="contentBox"><ul class="myNetworkInfo">
            <li><i class="eid"></i></li>
            <li class="${this.power == 1 ? "" : "hide"}">ID<i class="eid">${row.id
        }</i></li>
            <li><i>${row.networkType}</i></li>
             <li><i>${row.name}</i></li>
            <li><i>${row.address}</i></li>
          </ul></div>`;
      let infoWindow = new BMap.InfoWindow(html, opts); //
      myPolygon.addEventListener("click", function () {
        this.openInfoWindow(infoWindow);
        myPolygon = null;
      });
    },
    //
    // check=0 check=1
    //_t.networkRouteObj
    getRouteList() {
      let _t = this;
      _t.listLoading = true;
      roadlist({
        page: _t.listQuery_route.page,
        limit: _t.listQuery_route.limit
      })
        .then(res => {
          if (res.status == 0) {
            _t.listLoading = false;
            _t.list_route = res.data.records;
            _t.list_route.forEach((item, index) => {
              _t.list_route[index].index = index;
              if (_t.networkRouteObj.hasOwnProperty(item.geometryId)) {
                _t.list_route[index].check = 0;
              } else {
                _t.list_route[index].check = 1;
              }
              _t.list_route[index].createTime = formatDate(item.createTime);
            });
            _t.total_route = res.data.total;
            _t.indexMethod_route =
              (_t.listQuery_route.page - 1) * _t.listQuery_route.limit + 1;
          } else {
            _t.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },
    //
    checkRoute(row) {
      let _t = this,
        map = _t.map,
        geomList = [],
        lineOptions = {
          // 
          strokeOpacity: 1,
          strokeColor: "#3f9c0f",
          strokeWeight: 4
        },
        geomData = [];
      //id
      if (row.geometryId == null) {
        this.$message.warning('');
        return;

      }
      viewlist({ geometryId: row.geometryId }).then(res => {
        if (res.status == 0) {
          row.check = 0;
          _t.list_route.splice(row.index, 1, row);
          _t.networkRouteObj[row.geometryId] = 1;
          geomList = res.data.geom.split(",");
          geomList.forEach(x => {
            geomData.push(new BMap.Point(x.split(" ")[0], x.split(" ")[1]));
          });
          map.setViewport(geomData, { margins: [50, 50, 50, 50] });
          let polyline = new BMap.Polyline(geomData, lineOptions);
          map.addOverlay(polyline);
          // 
          let marker1 = new BMap.Marker(geomData[0], { icon: _t.markerStart }); // 
          map.addOverlay(marker1);
          let marker2 = new BMap.Marker(geomData[geomData.length - 1], {
            icon: _t.markerEnd
          }); // 
          map.addOverlay(marker2);
          let pointCollection = new BMap.PointCollection(geomData, {
            color: "#d340c3",
            size: BMAP_POINT_SIZE_SMALL
          });
          map.addOverlay(pointCollection);
          this.allRoute.push({
            polyline: polyline,
            marker1: marker1,
            marker2: marker2,
            pointCollection: pointCollection,
            geometryId: row.geometryId
          });
        } else {
          _t.$message.error(res.msg);
        }
      });
    },
    //
    //this.realtimeTrailObj 
    formateRealtimeTrailObj() {
      //
      let selectCarOrGunArray = this.selectCarArray.concat(this.selectGunArray);
      let realtimeTrailObj = { ...this.realtimeTrailObj };
      //
      for (let item in realtimeTrailObj) {
        if (
          !selectCarOrGunArray.find(function (elem) {
            return elem.id == item;
          })
        ) {
          for (let i = 0; i < realtimeTrailObj[item].polyline.length; i++) {
            this.map.removeOverlay(realtimeTrailObj[item].polyline[i]);
          }

          this.map.removeOverlay(this.carMkStartObj[item]);
          delete this.carMkStartObj[item];
          this.map.removeOverlay(this.carMkObj[item]);
          if (this.carMkObj[item]) {
            // this.map.removeOverlay(this.carMkObj[item].message.IconLabel);

          }

          delete this.carMkObj[item];
          delete this.realtimeTrailObj[item];
        }
      }
      //
      let realtimeTrailObjNew = { ...this.realtimeTrailObj };
      for (let i = 0; i < selectCarOrGunArray.length; i++) {
        if (!realtimeTrailObjNew.hasOwnProperty(selectCarOrGunArray[i].id)) {
          let imei = selectCarOrGunArray[i].id;
          this.realtimeTrailObj[selectCarOrGunArray[i].id] = {};
          this.realtimeTrailObj[selectCarOrGunArray[i].id].polyline = [];
          this.realtimeTrailObj[selectCarOrGunArray[i].id].lastPoint = null;
          this.realtimeTrailObj[selectCarOrGunArray[i].id].points = [[]];
        }
      }
    },
    //
    posToTrailAddStartMarker(makerIconArr) {
      //
      let selectCarOrGunArray = this.selectCarArray.concat(this.selectGunArray);
      //
      for (let i = 0; i < selectCarOrGunArray.length; i++) {
        let imei = selectCarOrGunArray[i].id;
        this.realtimeTrailObj[selectCarOrGunArray[i].id] = {};
        this.realtimeTrailObj[selectCarOrGunArray[i].id].polyline = [];
        this.realtimeTrailObj[selectCarOrGunArray[i].id].lastPoint = null;
        this.realtimeTrailObj[selectCarOrGunArray[i].id].points = [[]];
      }
      //
      for (let j = 0; j < makerIconArr.length; j++) {
        let obj = {};
        if (makerIconArr[j].type == 2) {
          obj = this.carIsOnlineArr;
        } else if (makerIconArr[j].type == 1) {
          obj = this.gunIsOnlineArr;
        }

        if (
          obj.hasOwnProperty(makerIconArr[j].imei) &&
          (obj[makerIconArr[j].imei] == 1 || obj[makerIconArr[j].imei] == 5)
        ) {
          //fake
          if (obj[makerIconArr[j].imei] == 1) {
            makerIconArr[j].fake = 0;
          } else if (obj[makerIconArr[j].imei] == 5) {
            makerIconArr[j].fake = 1;
          }

          this.addRealtimeTrail(
            makerIconArr[j],
            makerIconArr[j].type,
            obj[makerIconArr[j].imei]
          ); //
        }
      }
    },
    //
    //this.realtimeTrailObj this.realtimeTrailObj
    // this.realtimeTrailObj  lastPoint time isDotLast points
    //30s30ssetpath
    addRealtimeTrail(newdata1, gunOrCarType, status) {
      let newdata = { ...newdata1 };
      //
      let resultPoint; //
      let message = null; //
      //
      if (newdata.lng && newdata.lat) {
        let point = new BMap.Point(newdata.lng, newdata.lat);
        resultPoint = Gps2Bd(point);
        newdata.Blng = resultPoint.lng;
        newdata.Blat = resultPoint.lat;
      }
      //
      if (this.realtimeTrailObj.hasOwnProperty(newdata.imei)) {
        message = { ...this.realtimeTrailObj[newdata.imei] };
      }
      //15fake,

      if (
        (status == 1 || status == 5) &&
        newdata.lng &&
        newdata.lat &&
        this.realtimeTrailObj.hasOwnProperty(newdata.imei)
      ) {
        //message.lastPoint==null

        if (message.lastPoint == null) {
          //
          this.addStartMarker(resultPoint, "", newdata);
          message.lastPoint = resultPoint;
          message.time = newdata.time;
          message.isDot = false;
          message.points[0].push(
            new BMap.Point(resultPoint.lng, resultPoint.lat)
          ); //
          message.polyline = [];
          this.realtimeTrailObj[newdata.imei] = { ...message };
        }
        //  else {
        //

        //3
        let len1 = message.points.length; //
        let isDot = false; //
        let lastPointBD = new BMap.Point(
          message.lastPoint.lng,
          message.lastPoint.lat
        ); //
        let isAdd = true; //
        let Dval = newdata.time - message.time; //
        let isDotLast = message.isDot; //
        //30s
        //
        if (message.polyline.length == 0 && !(Dval > 180000)) {
          isDotLast = true;
        }
        //30s
        if (Dval > 180000 && isDotLast == false) {
          message.points.push([
            lastPointBD,
            new BMap.Point(resultPoint.lng, resultPoint.lat)
          ]); //
          message.isDot = true;
          isDot = true;
          isAdd = true;
          //30s
        } else if (Dval > 180000 && isDotLast == true) {
          message.points[len1 - 1].push(
            new BMap.Point(resultPoint.lng, resultPoint.lat)
          );
          isDot = true;
          isAdd = false;
          //30s
        } else if (!(Dval > 180000) && isDotLast == true) {
          message.points.push([
            lastPointBD,
            new BMap.Point(resultPoint.lng, resultPoint.lat)
          ]); //
          message.isDot = false;
          isDot = false;
          isAdd = true;
          //30s
        } else if (!(Dval > 180000) && isDotLast == false) {
          message.isDot = false;
          isDot = false;
          isAdd = false;
          message.points[len1 - 1].push(
            new BMap.Point(resultPoint.lng, resultPoint.lat)
          );
        }
        //
        this.drowLine(
          message,
          resultPoint,
          gunOrCarType,
          newdata,
          isDot,
          isAdd
        );
        message.lastPoint = resultPoint;
        message.time = newdata.time;
        this.realtimeTrailObj[newdata.imei] = { ...message };
        // }
        //fake
      } else if (
        (status !== 1 || (status == 1 && !newdata.lng && !newdata.lat)) &&
        (status !== 5 || (status == 1 && !newdata.lng && !newdata.lat)) &&
        this.realtimeTrailObj.hasOwnProperty(newdata.imei) &&
        message.lastPoint !== null
      ) {
        //
        if (
          !newdata.lng &&
          !newdata.lat &&
          this.carMkObj.hasOwnProperty(newdata.imei)
        ) {
          newdata.lng = this.carMkObj[newdata.imei].message.lng;
          newdata.lat = this.carMkObj[newdata.imei].message.lat;
          newdata.Blng = message.lastPoint.lng;
          newdata.Blat = message.lastPoint.lat;
        }

        //
        let obj = message;
        let PointArrNext = message.lastPoint;
        this.addMarkerEnd(
          new BMap.Point(PointArrNext.lng, PointArrNext.lat),
          gunOrCarType,
          newdata,
          status
        );
      }
    },
    //
    //notEmptyCarOrGun 
    delRealTimeTrail(notEmptyCarOrGun) {
      let realtimeTrailObj = this.realtimeTrailObj;
      for (let item in this.realtimeTrailObj) {
        if (realtimeTrailObj[item].polyline) {
          for (let i = 0; i < realtimeTrailObj[item].polyline.length; i++) {
            this.map.removeOverlay(realtimeTrailObj[item].polyline[i]);
          }
        }
        this.map.removeOverlay(this.carMkStartObj[item]);

        delete this.carMkStartObj[item];
        if (notEmptyCarOrGun !== "notEmptyCarOrGun") {
          this.map.removeOverlay(this.carMkObj[item]);
          // this.map.removeOverlay(this.carMkObj[item].message.IconLabel);

        }
        delete this.carMkObj[item];

        delete this.realtimeTrailObj[item];
      }
      this.realtimeTrailObj = {};
      this.carMkStartObj = {};
      this.carMkObj = {};
    },
    delMakerIcon() {
      for (let i = 0; i < this.makerIconArr.length; i++) {
        this.map.removeOverlay(this.makerIconArr[i].Icon);
        // this.map.removeOverlay(this.makerIconArr[i].IconLabel);

      }
      this.makerIconArr = [];
    },
    //
    addStartMarker(point, name, newdata) {
      if (name == "") {
        let myIcon = new BMap.Icon(mapIcon, new BMap.Size(26, 41), {
          anchor: new BMap.Size(13, 40),
          imageOffset: new BMap.Size(-199, -139)
        });
        let marker = new BMap.Marker(point, { icon: myIcon });
        this.map.addOverlay(marker); // 
        this.carMkStartObj[newdata.imei] = marker;
      }
    },
    // 
    // trailObj  PointArrNext gunOrCarType
    //newdata isDot isAddsetPath
    drowLine(trailObj, PointArrNext, gunOrCarType, newdata, isDot, isAdd) {
      let len1 = trailObj.points.length;
      let points = trailObj.points[len1 - 1];
      let polyline = null;
      if (isAdd) {
        if (isDot) {
          polyline = new BMap.Polyline(points, {
            strokeStyle: "dashed",
            strokeOpacity: 1,
            strokeColor: "#a7a7a7",
            strokeWeight: 4
          }); //
        } else {
          polyline = new BMap.Polyline(points, {
            strokeColor: "#3f9c0f",
            strokeWeight: 3,
            strokeOpacity: 1
          }); //
        }
        //
        let len = this.realtimeTrailObj[newdata.imei].polyline.length;
        this.realtimeTrailObj[newdata.imei].polyline.push(polyline);
        this.map.addOverlay(polyline);
      } else {
        // 
        let len = this.realtimeTrailObj[newdata.imei].polyline.length;
        this.realtimeTrailObj[newdata.imei].polyline[len - 1].setPath(points);
      }
      let fake = 1;
      //fake
      if (newdata.fake == 1) {
        fake = 5;
      }
      // 
      this.addMarkerEnd(
        new BMap.Point(PointArrNext.lng, PointArrNext.lat),
        gunOrCarType,
        newdata,
        fake
      );
    },
    //
    //point  gunOrCarType newdata statusfake
    addMarkerEnd(point, gunOrCarType, newdata, status, alarm) {
      let that = this;
      let end = null;
      //
      let onlineCar1 = onlineCar;
      let onlineGun1 = onlineGun;
      let offlineCar1 = offlineCar;
      let offlineGun1 = offlineGun;
      let fakecar1 = fakecar;
      let fakegun1 = fakegun;
      let gpsCar1 = gpsCar;
      let gpsGun1 = gpsGun;
      let aloneGun1 = aloneGun;
      let aloneCar1 = aloneCar;
      let noPositionCar1 = noPositionCar;
      let noPositionGun1 = noPositionGun;
      let aloneObj1 = JSON.parse(sessionStorage.aloneObj); //
      //status
      if (status == undefined) {
        if (gunOrCarType == 1) {
          end = onlineGun1;
        } else {
          end = onlineCar1;
        }
      } else {
        if (status == 0 || status == 2) {
          if (gunOrCarType == 2) {
            end = offlineCar1;
          } else {
            end = offlineGun1;
          }
        } else if (status == 3) {
          if (gunOrCarType == 2) {
            end = gpsCar2;
          } else {
            end = gpsGun2;
          }
        } else if (status == 1) {
          if (gunOrCarType == 2) {
            end = onlineCar1;
          } else {
            end = onlineGun1;
          }
        } else if (status == 4) {
          if (gunOrCarType == 2) {
            end = gpsCar1;
          } else {
            end = gpsGun1;
          }
        } else if (status == 5 && this.power == 1) {
          if (gunOrCarType == 2) {
            end = fakecar1;
          } else {
            end = fakegun1;
          }
        } else if (status == 5 && this.power !== 1) {
          if (gunOrCarType == 2) {
            end = onlineCar1;
          } else {
            end = onlineGun1;
          }
        }else if (status == 6) {
          if (gunOrCarType == 2) {
            end = noPositionCar1;
          } else {
            end = noPositionGun1;
          }
        }
      }



      if ((end == onlineCar1 || end == fakecar1) && that.speedObj[newdata.imei] == 1) {
        end = speedCar;
      }
      if ((end == onlineGun1 || end == fakegun1) && that.speedObj[newdata.imei] == 1) {
        end = speedGun;
      }

      //0
      //status
      if (
        aloneObj1.hasOwnProperty(newdata.imei) &&
        aloneObj1[newdata.imei] !== 10007
      ) {
        if (newdata.type == 1) {
          end = aloneGun1;
        } else if (newdata.type == 2) {
          end = aloneCar1;
        }
      } else if (
        aloneObj1.hasOwnProperty(newdata.imei) &&
        aloneObj1[newdata.imei] == 10007
      ) {
        if (newdata.type == 1) {
          end = gpsGun1;
        } else if (newdata.type == 2) {
          end = gpsCar1;
        }
      }
      //
      let terminalPoint = new BMap.Icon(end, this.bmapSize, {
        imageSize: this.bmapSize
      });
      //
      if (this.carMkObj.hasOwnProperty(newdata.imei) && alarm == 'alarm') {
        this.carMkObj[newdata.imei].setIcon(terminalPoint);
        if (newdata.type == 2) {
          this.carMkObj[newdata.imei].setRotation(this.carMkObj[newdata.imei].getRotation());
        }
        return;
      }
      // this.carMkObjmarker
      if (this.carMkObj.hasOwnProperty(newdata.imei)) {
        // newdata.IconLabel=this.carMkObj[newdata.imei].message.IconLabel;
        this.carMkObj[newdata.imei].message = newdata;
        //
        if (status == 1 || status == 5) {
          this.carMkObj[newdata.imei].setPosition(point);
          // this.carMkObj[newdata.imei].message.IconLabel.setPosition(point);

        }
        //
        this.carMkObj[newdata.imei].setIcon(terminalPoint);
        //
        if (
          (status == 1 || status == 5 || status == undefined) &&
          gunOrCarType == 2
        ) {
          if (newdata.direction !== undefined) {
            this.carMkObj[newdata.imei].setRotation(newdata.direction + 90); // 
          }
        } else {
          this.carMkObj[newdata.imei].setRotation(
            this.carMkObj[newdata.imei].getRotation()
          );
        }
      } else {
        //marker
        this.carMkObj[newdata.imei] = new BMap.Marker(point, {
          icon: terminalPoint
        }); // 
        this.carMkObj[newdata.imei].message = newdata;
        if (gunOrCarType == 2 && (status == undefined || status == 1)) {
          // 
          let direction = 0;
          if (newdata.direction !== undefined) {
            direction = newdata.direction + 90;
          } else {
            direction = -90;
          }
          this.carMkObj[newdata.imei].setRotation(direction); // 
        } else {
          this.carMkObj[newdata.imei].setRotation(
            that.carMkObj[newdata.imei].getRotation()
          );
        }
        this.addClickHandler(
          this.carMkObj[newdata.imei].message,
          this.carMkObj[newdata.imei],
          "addMarkerEnd"
        );
        let licenseNo = "";
        if (this.carAndGunObj.hasOwnProperty(newdata.imei)) {

          licenseNo = this.carAndGunObj[newdata.imei].code;
        }
        this.carMkObj[newdata.imei].setLabel(this.setLabelStyle(licenseNo));

        // this.carMkObj[newdata.imei].message.IconLabel=new BMap.Label(licenseNo,{position : point, offset: new BMap.Size(0,0)});
        this.map.addOverlay(this.carMkObj[newdata.imei]); // 

        // this.map.addOverlay(this.carMkObj[newdata.imei].message.IconLabel); // 



      }

      if (end == aloneGun1 || end == aloneCar1) {
        this.carMkObj[newdata.imei].setZIndex(2);
      } else {
        this.carMkObj[newdata.imei].setZIndex(1);
      }
      //
      if (newdata.imei == this.followCarOrGun) {
        this.bound = this.map.getBounds();
        if (!this.bound.containsPoint(point)) {
          this.map.panTo(point);
        }
      }
      // 
      if (
        newdata.imei === this.richMarkerImeiIndex &&
        (status == undefined || status == 1 || status == 5)
      ) {
        this.openInfo(newdata, "changeRichMarkerInfo");
      } else if (newdata.imei === this.richMarkerImeiIndex) {
        this.openInfo(newdata, "changerRealTrail");
      }
    },
    //
    addSelected(data, type) {
      let message = { ...data.message };
      if (this.selectObj.hasOwnProperty(message.imei)) {

        this.addCarToMap(message, type);
      }
    },
    //
    getOffline(val) {
      this.currpage1 = 1;
      if (val == 2) {
        this.offlineTimeoutArr = this.carOfflineTimeoutArr;
      } else {
        this.offlineTimeoutArr = this.gunOfflineTimeoutArr;
      }
    },
    //
    getLastGps(type) {
      let arr = [];
      if (type == 2) {
        arr = [...this.allCar];
      } else {
        arr = [...this.allGun];
      }
      lastgps(arr).then(res => {
        if (res && res.status == 0) {
          if (res.data !== [] && res.data) {
            this.getOfflineTimeout(res.data, type);
          }
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    //
    getOfflineTimeout(infos, type) {
      let offlineTimeoutArr = [];
      let timeNow = Date.parse(new Date());
      for (let i = 0; i < infos.length; i++) {
        let gpsMes = infos[i];
        if (gpsMes == null) {
          if (type == 1) {
            offlineTimeoutArr.push({ imei: this.allGun[i] });
          } else {
            offlineTimeoutArr.push({ imei: this.allCar[i] });
          }
          continue;
        }

        let interval = parseInt((timeNow - parseInt(gpsMes.time)) / 1000);
        if (interval > 2 * 24 * 3600) {
          let day = 0,
            hour = 0,
            minute = 0,
            second = 0;
          day = parseInt(interval / (24 * 3600));
          hour = parseInt((interval % (24 * 3600)) / 3600);
          minute = parseInt(((interval % (24 * 3600)) % 3600) / 60);
          second = parseInt(((interval % (24 * 3600)) % 3600) % 60);
          gpsMes.offlineTime =
            day + "" + hour + "" + minute + "" + second + "";
          offlineTimeoutArr.push(gpsMes);
        }
      }
      if (type == 2) {
        this.carOfflineTimeoutArr = offlineTimeoutArr;
      } else {
        this.gunOfflineTimeoutArr = offlineTimeoutArr;
      }
      this.offlineTimeoutArr = this.carOfflineTimeoutArr;
    },
    //
    getCountDown(time) {
      let timeNow = Date.parse(new Date());
      let interval = parseInt((timeNow - parseInt(time)) / 1000);
      if (interval > 0) {
        let day = "00",
          hour = "00",
          minute = "00",
          second = "00";
        day = parseInt(interval / (24 * 3600));
        hour = parseInt((interval % (24 * 3600)) / 3600);
        minute = parseInt(((interval % (24 * 3600)) % 3600) / 60);
        second = parseInt(((interval % (24 * 3600)) % 3600) % 60);
        if (day) {
          return day + "" + hour + "" + minute + "" + second + "";
        } else {
          return hour + "" + minute + "" + second + "";
        }
      } else {
        return 0;
      }
    },
    //
    //type 2: 1  trailToPos 
    getCarOrGunLastGps(type, trailToPos) {
      let that = this,
        elSelect = [];
      if (type == 2) {
        elSelect = this.elSelectCarArray;
      } else if (type == 1) {
        elSelect = this.elSelectGunArray;
      }
      lastgps(elSelect).then(res => {
        if (res && res.status == 0) {
          if (res.data !== [] && res.data) {
            //
            that.carOrGunSelectedChange(res.data, type);
            //
            this.setView();
            //
            this.setZindex(this.followCarOrGun);
          }
          //
          if (!trailToPos) {
            $(".p" + that.richMarkerImeiIndex).click();
          }
        } else {
          this.$message.error(res.msg);
        }
      });
    },

    //makerIconArrmarker
    //carAndGunArr=['1','2','3','4','5']
    //markerArr=[{imei:'1',Icon:''},{imei:'3',Icon:''},{imei:'2',Icon:''},{imei:'7',Icon:''}]
    retainRepeat(carAndGunArr, markerArr) {
      let newMarkerArr = [];
      for (let i = 0; i < markerArr.length; i++) {
        let hasRepeat = false;
        for (let k = 0; k < carAndGunArr.length; k++) {
          if (markerArr[i].imei == carAndGunArr[k]) {
            newMarkerArr.push(markerArr[i]);
            hasRepeat = true;
            break;
          }
        }
        if (hasRepeat == false) {
          this.map.removeOverlay(markerArr[i].Icon);
          // this.map.removeOverlay(markerArr[i].IconLabel);

        }
      }
      this.makerIconArr = newMarkerArr;
    },

    // type=1type=2
    //this.gunIsOnlineArrfake,0
    //this.carIsOnlineArrfake,0

    formatMarkerArr(infos, carAndGun, type) {
      let arr = [];

      let gunOrCarIsOnlineObj = {};
      if (type == 1) {
        gunOrCarIsOnlineObj = this.gunIsOnlineArr;
      } else if (type == 2) {
        gunOrCarIsOnlineObj = this.carIsOnlineArr;
      }
      for (let i = 0; i < infos.length; i++) {
        let exType = 0;
        if (infos[i] == null) {
          continue;
        }
        //this.gunIsOnlineArr0
        //
        if (!gunOrCarIsOnlineObj.hasOwnProperty(infos[i].imei)) {
          gunOrCarIsOnlineObj[infos[i].imei] = 0;
        }
        if (
          gunOrCarIsOnlineObj[infos[i].imei] == 1 ||
          gunOrCarIsOnlineObj[infos[i].imei] == 5
        ) {
          exType = 4;
        } else if (gunOrCarIsOnlineObj[infos[i].imei] == 2) {
          exType = 0;
        } else if (gunOrCarIsOnlineObj[infos[i].imei] == 3) {
          exType = 1;
        } else if (gunOrCarIsOnlineObj[infos[i].imei] == 4) {
          exType = 3;
        }else if (gunOrCarIsOnlineObj[infos[i].imei] == 6) {
          exType = 6;
        }

        arr.push({
          message: {
            imei: infos[i].imei,
            type: type,
            lng: infos[i].lng,
            lat: infos[i].lat,
            time: infos[i].time,
            address: null,
            speed: infos[i].speed,
            battery: null,
            direction: null,
            exType: exType,
            number:infos[i].number,
            rfids:infos[i].rfids,
          }
        });
      }
      return arr;
    },
    //
    //data  type=1type=2
    carOrGunSelectedChange(data, type) {
      let that = this;
      let carAndGunSelectedArr = that.elSelectCarArray.concat(
        that.elSelectGunArray
      );
      let iconArr = [...that.makerIconArr];
      //
      let markerArr = that.formatMarkerArr(data, carAndGunSelectedArr, type);
      //
      that.retainRepeat(carAndGunSelectedArr, that.makerIconArr);
      //
      for (let i = 0; i < markerArr.length; i++) {
        let hasIcon = iconArr.find(function (elem) {
          return elem.imei == markerArr[i].message.imei;
        });
        if (!hasIcon) {
          let arr = {};
          let arrtype = 0;
          if (markerArr[i].message.type == 2) {
            arr = that.carIsOnlineArr;
          } else if (markerArr[i].message.type == 1) {
            arr = that.gunIsOnlineArr;
          }
          if (arr[markerArr[i].message.imei] !== undefined) {
            arrtype = arr[markerArr[i].message.imei];
          }
          //marker
          that.addSelected(markerArr[i], arrtype);
        }
      }
    },
    //
    setView() {
      let type = this.selectAllOrPart;
      let data = this.makerIconArr;
      let pointArr = [];
      let convertorData = [];
      if (this.selectAllOrPart == "all") {
        for (let i = 0; i < data.length; i++) {
          let ggPoint = new BMap.Point(data[i].lng, data[i].lat);
          pointArr.push(ggPoint);
        }
      } else if (this.selectAllOrPart == "part") {
        for (let i = 0; i < data.length; i++) {
          if (data[i].imei == this.selectImei) {
            let ggPoint = new BMap.Point(data[i].lng, data[i].lat);
            pointArr.push(ggPoint);
            break;
          }
        }
      }

      convertorData = Gps2Bds(pointArr);
      this.setZoom(convertorData);
      this.selectAllOrPart = null;
    },
    // 
    setZoom(bPoints) {
      let view = this.map.getViewport(eval(bPoints));
      let mapZoom = view.zoom;
      let centerPoint = view.center;
      this.map.centerAndZoom(centerPoint, mapZoom);
    },
    //
    initializeFollow() {
      // imei
      let myImei = this.richMarkerImeiIndex;
      let followCarOrGun = this.followCarOrGun;
      //-1
      if (
        myImei != undefined &&
        this.elSelectCarArray.indexOf(myImei) < 0 &&
        this.elSelectGunArray.indexOf(myImei) < 0
      ) {
        this.myRichMarkerDraged = false;
        this.myRichMarker.hide();
        this.richMarkerImeiIndex = -1;
      }
      //
      if (
        followCarOrGun &&
        this.elSelectCarArray.indexOf(followCarOrGun) < 0 &&
        this.elSelectGunArray.indexOf(followCarOrGun) < 0
      ) {
        //tree-car-selected
        $("span").removeClass("tree-car-selected");
        $(".trail").remove();
        this.followCarOrGun = null;
      }
    },
    //tree
    filterNode(value, data, node) {
      value = value.trim();
      if (
        !value ||
        data.label.indexOf(value) !== -1 ||
        (data.devno !== undefined && data.devno.indexOf(value) !== -1)
      )
        return true;
      // 
      return this.checkBelongToChooseNode(value, data, node);
    },
    // 
    checkBelongToChooseNode(value, data, node) {
      const level = node.level;
      // 
      if (level === 1) return false;
      // 
      let parentData = node.parent;
      // 
      let index = 0;
      while (index < level - 1) {
        // 
        if (
          parentData.data.label.indexOf(value) !== -1 ||
          (parentData.data.devno !== undefined &&
            parentData.data.devno.indexOf(value) !== -1)
        ) {
          return true;
        }
        // 
        parentData = parentData.parent;
        index++;
      }
      // false
      return false;
    },
    // 
    showMenu() {
      this.toshow = !this.toshow;
    },
    //
    setZindex(followCarOrGun) {
      if (this.realTimeTrailValue == false) {
        let makerIcon = this.makerIconArr;
        for (let i = 0; i < makerIcon.length; i++) {
          if (followCarOrGun == makerIcon[i].imei) {
            makerIcon[i].Icon.setZIndex(2);
          } else {
            makerIcon[i].Icon.setZIndex(1);
          }
        }
      } else {
        let makerIcon = this.carMkObj;
        for (let item in makerIcon) {
          if (followCarOrGun == item) {
            makerIcon[item].setZIndex(2);
          } else {
            makerIcon[item].setZIndex(1);
          }
        }
      }
    },
    //tree
    //:info imei: hasChecked
    checkNodes(info, imei, hasChecked) {
      let type; // 1 2
      let that = this;
      let array = []; //
      let isChecked = false;
      let tree;
      let typeText;
      if (typeof info !== 'number') {
        typeText = this.formatCarOrGunType(info); //
        //this.carAndGunObj
        if (!this.carAndGunObj.hasOwnProperty(imei)) {
          return;
        }
        if (typeText == "") {
          type = 2;
        } else if (typeText == "") {
          type = 1;
        }
      } else if (typeof info == 'number') {
        type = info;
      }

      if (type == 2) {
        tree = this.$refs.tree;
      } else if (type == 1) {
        tree = this.$refs.treeGun;
      }
      //
      array = tree.getCheckedNodes(false, false).filter(item => {
        return item.type == "device";
      });
      // 
      for (let i = 0; i < array.length; i++) {
        if (array[i].id == imei) {
          isChecked = true;
        }
      }
      //, 
      if (isChecked && hasChecked) {
        if (this.followCarOrGun !== imei) {
          $(".p" + imei).click();
        }
        return;
      }
      //
      if (isChecked == false) {
        array.push({
          id: imei,
          type: "device"
        });
        //
        tree.setCheckedNodes(array, false);
      }

      // 
      let newArray = tree.getCheckedNodes(false, false);
      let carOrgunArray = [];

      let filterArray = newArray.filter(item => {
        return item.type == "device";
      });

      if (type == 2) {
        this.selectCarArray = filterArray;
        carOrgunArray = this.selectCarArray;
      } else if (type == 1) {
        this.selectGunArray = filterArray;
        carOrgunArray = this.selectGunArray;
      }

      let optionsArray = [];
      if (filterArray) {
        filterArray.forEach((item1, index1) => {
          optionsArray.push(item1.id);
        });
        if (type == 2) {
          this.elSelectCarArray = optionsArray;
        } else if (type == 1) {
          this.elSelectGunArray = optionsArray;
        }
      }
      //
      this.selectAllOrPart = "part";
      this.selectImei = imei;
      this.followCarOrGun = imei;
      this.richMarkerImeiIndex = imei;
      if (!this.carMkObj.hasOwnProperty(imei)) {
        that.myRichMarkerDraged = false;
        that.myRichMarker.hide();
      }
      //
      //
      //
      //
      //bug,
      if (this.realTimeTrailValue == true) {
        $(".p" + imei).click();
      }
      this.changeToRedAlarm();
    },

    //
    //1.sessionStorage.aloneObj)
    //2.
    //3.
    changeToRedAlarm() {
      let that = this;
      let aloneGun1 = aloneGun; //
      let aloneCar1 = aloneCar; //
      let gpsCar1 = gpsCar; //
      let gpsGun1 = gpsGun; //
      this.aloneObj = {};
      let newWaringData1 = [...this.newWaringData]; //
      let makerIconArr = [...this.makerIconArr]; //

      //
      for (let i = 0; i < newWaringData1.length; i++) {
        let status = newWaringData1[i].status;
        if (
          (newWaringData1[i].status == 10003 ||
            newWaringData1[i].status == 10011 ||
            newWaringData1[i].status == 10006 ||
            newWaringData1[i].status == 10007) &&
          newWaringData1[i].isCancled == false
        ) {
          // ,addCarToMap0
          //
          //this.aloneObj
          if (!this.aloneObj.hasOwnProperty(newWaringData1[i].imei)) {
            this.aloneObj[newWaringData1[i].imei] = status;
          }
        }
      }
      sessionStorage.setItem("aloneObj", JSON.stringify(this.aloneObj));

      //
      for (let k = 0; k < makerIconArr.length; k++) {
        let type1;
        if (makerIconArr[k].type == 2) {
          type1 = this.carIsOnlineArr[makerIconArr[k].imei];
        } else if (makerIconArr[k].type == 1) {
          type1 = this.gunIsOnlineArr[makerIconArr[k].imei];
        }
        this.addCarToMap({ imei: makerIconArr[k].imei, type: makerIconArr[k].type }, type1, 'alarm')
      }
      //
      for (let item1 in this.carMkObj) {
        let type1, type2 = this.carAndGunObj[item1].type;
        if (type2 == 2) {
          type1 = this.carIsOnlineArr[item1];
        } else if (type2 == 1) {
          type1 = this.gunIsOnlineArr[item1];
        }
        this.addMarkerEnd(null, type2, { imei: item1, type: type2 }, type1, 'alarm')
