    //
    cancle(imei) {
      let warningData = this.newWaringData;

      for (let j = warningData.length - 1; j >= 0; j--) {
        if (
          warningData[j].status == 10011 &&
          warningData[j].imei == imei &&
          warningData[j].isCancled == false
        ) {
          warningData[j].isCancled = true;
          warningData.splice(j, 1);
          sessionStorage.setItem("warningData", JSON.stringify(warningData));
          this.$store.dispatch("realTimeMonitor/SAVE_WARNINGDATA");
          this.$store.commit("realTimeMonitor/ISADDNEWAlARM", false); //false
          break;
        }
      }
