    //this.selectObj 1
    getSelectObj() {
      let obj = {};
      for (let k = 0; k < this.selectCarArray.length; k++) {
        let id = this.selectCarArray[k].id;
        if (!obj.hasOwnProperty(id)) {
          obj[id] = 1;
        }
      }
      for (let j = 0; j < this.selectGunArray.length; j++) {
        let id = this.selectGunArray[j].id;
        if (!obj.hasOwnProperty(id)) {
          obj[id] = 1;
        }
      }
      this.selectObj = { ...obj };
    },
    //
    // data
    getOnly(data) {
      let obj = {};
      let arr = [];
      if (data !== null) {
        arr = [...data];
      }
      arr = arr.reduce(function (item, next) {
        obj[next.id] ? "" : (obj[next.id] = true && item.push(next));
        return item;
      }, []);
      return arr;
    },
    //
    addTerminalIcon(message) {
      if (!message.lng || !message.lat) {
        return;
      }
      let that = this;
      let repeatIndex = null; //imei
      let img = terminal; //
      let ggPoint = {}; //
      let convertorData = []; //
      let Blng = null; //
      let Blat = null;
      let Icon = null; //marker
      let point = {}; //
      let pointArr = [];
      for (let index = 0; index < that.terminalIcon.length; index++) {
        if (that.terminalIcon[index].id == message.id) {
          repeatIndex = index;
          break;
        }
      }

      ggPoint = new BMap.Point(message.lng, message.lat);
      pointArr.push(ggPoint);
      convertorData = Gps2Bds(pointArr);

      if (convertorData.length !== 0) {
        Blng = convertorData[0].lng;
        Blat = convertorData[0].lat;
        point = new BMap.Point(Blng, Blat);
        let marker = new BMap.Icon(img, this.bmapSize, {
          imageSize: this.bmapSize
        });

        //lng,latgps
        let lng = message.lng;
        let lat = message.lat;

        if (repeatIndex !== null) {
          that.terminalIcon[repeatIndex].Icon.setPosition(point);
          that.terminalIcon[repeatIndex].lng = message.lng;
          that.terminalIcon[repeatIndex].lat = message.lat;
          that.terminalIcon[repeatIndex].content = message.content;
          that.terminalIcon[repeatIndex].time = message.time;
          that.terminalIcon[repeatIndex].id = message.id;
          that.terminalIcon[repeatIndex].Blng = Blng;
          that.terminalIcon[repeatIndex].Blat = Blat;

          // 
          if (message.id === this.richMarkerImeiIndex) {
            that.openInfoTerminal(that.terminalIcon[repeatIndex]);
          }
        } else {
          Icon = new BMap.Marker(point, {
            icon: marker,
            title: message.id
          });

          message.Icon = Icon;

          message.Blng = Blng;
          message.Blat = Blat;

          let a = { ...message };

          that.terminalIcon.push(a);
          that.addClickHandlerTerminal(message, Icon);
          that.map.addOverlay(Icon);
        }
      } else {
      }
    },
