          // }
           for (let i1 = 0; i1 < this.selectAlarmObjsBtn.length; i1++) {
             let time=this.formatDate1(parseInt(this.selectAlarmObjsBtn[i1].info.time));
             let uuid=this.selectAlarmObjsBtn[i1].info.uuid;
             let id=this.selectAlarmObjsBtn[i1].info.id;
             if(uuid){
                if (!subData.hasOwnProperty('uuidMap')) {
                  subData.uuidMap= {}
                } 
                if (!subData.uuidMap.hasOwnProperty(time)) {
                  subData.uuidMap[time] = [uuid];
                } else {
                  subData.uuidMap[time].push(uuid);
                }
             }else{
               if (!subData.hasOwnProperty('idMap')) {
                  subData.idMap= {}
                } 
                 if (!subData.idMap.hasOwnProperty(time)) {
                  subData.idMap[time] = [id];
                } else {
                  subData.idMap[time].push(id);
                }
             }
            // if (!tableNames.hasOwnProperty(this.selectAlarmObjsBtn[i1])) {
            //   tableNames[time] = [this.selectAlarmObjsBtn[i1]];
            // } else {
            //   tableNames[time].push(this.selectselectAlarmObjsBtnAlarmBtn[i1]);
            // }
          }
          subData.handlerInfo= this.dealTemp.info
          alarmHandler(
            subData
            // {
            //   uuids: tableNames,
            //   info: { handlerInfo: this.dealTemp.info }
            // }
          ).then(res => {
            if (res.status == 200) {
