    renewWaningData() {
      let data = [...this.newWaringData];
      // //
      // for (let i = data.length - 1; i >= 0; i--) {
      //   for (let j = 0; j < this.selectAlarmBtn.length; j++) {
      //     if (data[i].info && this.selectAlarmBtn[j] == data[i].info.uuid) {
